@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(75, 73, 73);
  color: white;
  font-family: 'Open Sans', sans-serif;
  text-transform: capitalize;
}

.card-red{
    border-top: 10px solid rgb(204, 16, 52);
    border-radius: 10px !important;
    cursor: pointer;
}

.card-green{
    border-top: 10px solid #7dd71d;
    border-radius: 10px !important;
    cursor: pointer;
}

.h2{
    font-size: 25px !important;
}

.h2-time{
    font-size: 17px !important;
    margin-left: 10px;
}


@media screen and (max-width:640px) and (min-width:360px) {
    .card-red{
        width: 300px;
        margin: 10px 10px 0px 10px;
    }

    .card-green{
        width: 300px;
        margin: 10px 10px 0px 10px ;
    }
}

@media screen and (max-width:1024px) and (min-width:768px) {
    .card-red{
        /* width: 240px; */
        margin: 10px 10px 0px 10px;
    }

    .card-green{
        /* width: 240px; */
        margin: 10px 10px 0px 10px ;
    }
}
.table{
  margin-top: 20px;
  overflow:auto ;
  color: #6a5d5d;
  background-color: white;
  height: 300px;
}

.table::-webkit-scrollbar{
    overflow: hidden;
}

td{
    padding: 10px;
    margin: 10px;
}

tr:nth-child(odd){
    background-color: #e4e4e4;
}
.lineGraph{
    height: 250px;
}
.map{
    height: 450px;
}

.leafletmap{
    height: 100%;
    background-color: white;
    border-radius: 10px;
}

.popup-flag{
    width: 100%;
    height: 100px;
    background-repeat: no-repeat;
    background-size: cover;
}

.popup-name{
    margin: 5px;
    font-size: 20px;
}
.app{
    display: flex;
    justify-content: space-evenly;
    flex-wrap:wrap;
    padding: 20px;
}

.container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.app_left{
    width: 70%;
}


.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-title{
    letter-spacing: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-img>img{
    width: 50px;
    object-fit: contain;
    margin-right: 10px;
}

.header-corona{
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__dropdown{
    background-color: white;
    border-radius: 10px;
}

.card{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0px;
}

.app_right{
    border-radius: 10px !important;
}

h3{
    letter-spacing: 1px;
}


@media screen and (max-width:640px) and (min-width:360px) {
    .app{
        padding: 0;
    }

    .app_left{
        width: 100%;
        padding: 10px;
        font-size: 10px;
    }

    .header-img>img{
        width: 50px;
    }

    .header-dropdown{
        margin-left: 10px;
    }
    
    .card{
        flex-wrap: wrap;
        justify-content: center;
    }

    .app_right{
        padding: 10px;
    }

}


@media screen and (max-width:1024px) and (min-width:768px) {
    .app{
        padding: 0;
    }

    .app_left{
        width: 100%;
        padding: 10px;
        font-size: 10px;
    }

    .header-img>img{
        width: 50px;
    }

    .header-dropdown{
        margin-left: 10px;
    }
    
    .card{
        flex-wrap: wrap;
        justify-content: center;
    }

    .app_right{
        padding: 10px;
    }

}
